import Vue from 'vue';
import Vuex from 'vuex';
import { startWith } from 'rxjs/operators';

Vue.use(Vuex);

var _types = {
	'ADD_TO_CART': 1
};

const ADD_TO_CART = 'ADD_TO_CART';
const SUBTOTAL = 'SUBTOTAL';
const REMOVE_FROM_CART = 'REMOVE_FROM_CART';
const EMPTY_CART = 'EMPTY_CART';

export default new Vuex.Store({
	state: {
		cart: { items: [], subTotal: 0 },
  	},

  	mutations: {
		[ADD_TO_CART] (state, product) {
			var is_present = false;
			var items = state.cart.items.map(p => {
				if(p.title == product.title) { 
					is_present = true;
					p = {...p, qty: p.qty + product.qty};
				}
				return p;
			});
			if(!is_present) items.push(product);
			state.cart.items = [ ...items];
		},

		[REMOVE_FROM_CART](state, title) {
			var items = state.cart.items;
			var index = items.findIndex(p => p.title == title );
			items.splice(index, 1);
			state.cart.items = [...items];
		},

		[SUBTOTAL] (state) {
			var sum: number = state.cart.items.reduce((total, p) => total + (p.qty * p.price), 0);
			state.cart.subTotal = parseFloat((sum).toFixed(2));
		},
		
		[EMPTY_CART](state) {
			state.cart.items = [];
		}
  	},

  	actions: {
		[ADD_TO_CART]({ commit }, payload) {		
			commit(ADD_TO_CART, payload.product);
			commit(SUBTOTAL);
		},

		[REMOVE_FROM_CART]({ commit }, payload) {		
			commit(REMOVE_FROM_CART, payload.product);
			commit(SUBTOTAL);
		},

		[EMPTY_CART]({commit}) {
			commit(EMPTY_CART);
			commit(SUBTOTAL);
		}
  	}
});
