
























































































import { Component, Vue, Watch } from 'vue-property-decorator';
//import APPLICATION from '@/service/application.service';

@Component
export default class Navbar extends Vue {
    private dropdownActive:boolean = false;
    menuActive:boolean = false;
    mobile_nav:boolean = false;

    public togglenav() { this.mobile_nav = !this.mobile_nav; }

    @Watch('$route') public changeRoute() {
        this.dropdownActive = false;
        this.mobile_nav = false;
       document.body.click();
    }
}
